<template>
  <section class="hero" style="height: 100%;">
    <router-view></router-view>
  </section>
</template>

<script>
import {mapState} from 'vuex';
import StonkBrand from './components/StonkBrand';

export default {
  components: {StonkBrand},
  computed: mapState([
    'account'
  ]),
  mounted() {
    const previouslySelectedWallet = localStorage.getItem("selectedWallet");

    if (
      previouslySelectedWallet != null &&
      previouslySelectedWallet !== "null"
    ) {
      this.$store.dispatch("bootstrap");
    } else if (this.$router.currentRoute.path == '/claim') {
      this.$router.push({name: 'Home'});
    }

    this.$store.dispatch('retrievePrices');
  },
};
</script>

