<script>
  import {mapGetters} from 'vuex';
  import AnimatedNumber from "animated-number-vue";
  import MainNav from '../components/MainNav';
  import StonkBrand from '@/components/StonkBrand';
  import Spinner from "@/components/Spinner";
  import moment from 'moment';

  const _10_SECONDS = 1 * 10 * 1000;

  const SECONDS_IN_ONE_YEAR_APPROX = 31536000;

  export default {
    components: {
      MainNav,
      AnimatedNumber,
      Spinner,
      StonkBrand,
    },
    data() {
      return {
        amtInEth: {
          "0": null,
          "1": null,
          "2": null,
        },
        polling: null
      };
    },
    computed: {
      ...mapGetters([
        'contracts',
        'account',
        'stakingInfo',
        'stakingRewardTokenPriceInUsd',
        'stakingTokenPriceInUsd',
        'stakingReserveUSD',
        'rewardPerToken',
        'rewardRate',
      ]),
      styleHeight() {
        if (this.stakingInfo) {
          const res = this.stakingInfo.filter((si) => !si.deprecated || si.hasStake);

          return {height: `${400 + ((res.length + 1) * 100)}px`};
        }

        return {height: '600px'};
      },
    },
    mounted() {
      this.getStakingInfoForUser();

      // poll for updates every second
      this.polling = setInterval(this.getStakingInfoForUser, _10_SECONDS);
    },
    beforeDestroy() {
      clearInterval(this.polling);
    },
    methods: {
      async getStakingInfoForUser() {
        await this.$store.dispatch('getStakingInfoForUser');
      },
      async getRewards(index) {
        await this.$store.dispatch('stakingGetRewards', index);
      },
      async stake(index) {
        await this.$store.dispatch('stake', {amtInEth: this.amtInEth[index.toString()], index});
      },
      async withdraw(index) {
        await this.$store.dispatch('withdraw', {amtInEth: this.amtInEth[index.toString()], index});
      },
      async maxUnstaked(index) {
        if (this.stakingInfo) {
          this.amtInEth[index.toString()] = this.stakingInfo[index].unstakedTokenBalance;
        }
      },
      async maxStaked(index) {
        if (this.stakingInfo) {
          this.amtInEth[index.toString()] = this.stakingInfo[index].stakedTokenBalance;
        }
      },
      to9DpAndCurrencyFormatted(value) {
        const to9dDp = this.$options.filters.to9Dp(value);
        const formatted = this.$options.filters.currency(to9dDp, {fractionCount: '9'});
        return `${formatted}`;
      },
      async approve(index) {
        await this.$store.dispatch('approveStakingTokens', index);
      },
      connect() {
        this.$store.dispatch("bootstrap", {isChange: false});
      },
      unixToDate(value) {
        return moment.unix(value);
      },
      apr(index) {
        if (this.stakingRewardTokenPriceInUsd && this.rewardRate && this.stakingReserveUSD) {
          return this.stakingRewardTokenPriceInUsd * this.rewardRate[index] * SECONDS_IN_ONE_YEAR_APPROX / this.stakingReserveUSD[index] * 100;
        }

        return null;
      },
    },
    watch: {
      contracts(newVal, oldVal) {
        if (newVal !== oldVal) {
        }
      }
    },
  };
</script>


