Vue.filter("shortEth", function(value) {
  if (!value) return value;

  return `
  ${value.substr(0, 7)}...${value.substr(value.length - 5, value.length)}
  `;
});

new Vue({
  router,
  store,
  // apolloProvider,
  render: (h) => h(App),
}).$mount("#app");
