import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import { ethers } from "ethers";

import axios from "axios";
import client from "graphql-client";

const MAX_UINT256 = ethers.BigNumber.from("2")
  .pow(ethers.BigNumber.from("256"))
  .sub(ethers.BigNumber.from("1"));

// import VestingContractABI from "../contracts/VestingContractABI.json";
// import DeployedVestingContractAddresses from "../contracts/DeployedVestingContractAddresses.json";

// import VestingContractWithoutDelegationABI from "../contracts/VestingContractWithoutDelegationABI.json";
// import DeployedVestingContractWithoutDelegationAddresses from "../contracts/DeployedVestingContractWithoutDelegationAddresses.json";

import DeployedStakingRewardsContractAddresses from "../contracts/DeployedStakingRewardsContractAddresses.json";
import IStakingRewardsABI from "../contracts/IStakingRewardsABI.json";

import DeployedStakingRewardsTokenContractAddresses from "../contracts/DeployedStakingRewardsTokenContractAddresses.json";
import DeployedStakingTokenContractAddresses from "../contracts/DeployedStakingTokenContractAddresses.json";
import ERC20ABI from "../contracts/ERC20ABI.json";

import { initOnboard, initNotify } from "@/services/BlocknativeServices";
import moment from "moment";

let provider;
const onboard = initOnboard({
  wallet: (wallet) => {
    provider = new ethers.providers.Web3Provider(wallet.provider);

    // save any metamask connection for future visits to the site
    if (wallet.name === "MetaMask") {
      localStorage.setItem("selectedWallet", wallet.name);
    }
  },
});

const notify = initNotify();

export default new Vuex.Store({
  state: {
    chain: null,
    account: null,
    contracts: null,
    stakingRewardsContracts: null,
    stakingRewardsTokenContract: null,
    stakingTokenContracts: null,
    vestingSchedules: [],
    loadingVestingSchedules: true,
    stakingInfo: null,
    stakingRewardTokenPriceInUsd: null,
    stakingTokenPriceInUsd: null,
    stakingReserveUSD: null,
    rewardPerToken: null,
    rewardRate: null,
  },
  mutations: {
    setAccount(state, account) {
      state.account = account;
    },
    setChain(state, chain) {
      state.chain = chain;
    },
    setContracts(state, contracts) {
      state.contracts = contracts;
    },
    setStakingRewardsContracts(state, stakingRewardsContracts) {
      state.stakingRewardsContracts = stakingRewardsContracts;
    },
    setStakingRewardsTokenContract(state, stakingRewardsTokenContract) {
      state.stakingRewardsTokenContract = stakingRewardsTokenContract;
    },
    setStakingTokenContracts(state, stakingTokenContracts) {
      state.stakingTokenContracts = stakingTokenContracts;
    },
    setVestingSchedules(state, vestingSchedules) {
      state.vestingSchedules = vestingSchedules;
      state.loadingVestingSchedules = false;
    },
    setStakingInfo(state, stakingInfo) {
      state.stakingInfo = stakingInfo;
    },
    setStakingRewardTokenPriceInUsd(state, stakingRewardTokenPriceInUsd) {
      state.stakingRewardTokenPriceInUsd = stakingRewardTokenPriceInUsd;
    },
    setStakingTokenPriceInUsd(state, stakingTokenPriceInUsd) {
      state.stakingTokenPriceInUsd = stakingTokenPriceInUsd;
    },
    setStakingReserveUSD(state, stakingReserveUSD) {
      state.stakingReserveUSD = stakingReserveUSD;
    },
    setRewardPerToken(state, rewardPerToken) {
      state.rewardPerToken = rewardPerToken;
    },
    setRewardRate(state, rewardRate) {
      state.rewardRate = rewardRate;
    },
  },
  actions: {
    async bootstrap(
      { dispatch, commit },
      { isChange, onSuccessCallback } = {}
    ) {
      // check if a wallet was previously selected and use that
      const previouslySelectedWallet = localStorage.getItem("selectedWallet");

      let userSelectedAWallet;
      if (
        previouslySelectedWallet != null &&
        previouslySelectedWallet !== "null" &&
        !isChange
      ) {
        userSelectedAWallet = await onboard.walletSelect(
          previouslySelectedWallet
        );
      } else {
        userSelectedAWallet = await onboard.walletSelect();
      }

      // True if either a wallet was previously selected or was now selected from the modal
      // Will be false if the user closes the modal before selecting a wallet
      if (userSelectedAWallet) {
        if (isChange) {
          localStorage.removeItem("selectedWallet");
          commit("setAccount", null);
        }
        // Check wallet is ready to transact
        const readyToTransact = await onboard.walletCheck();

        if (readyToTransact) {
          const onboardState = onboard.getState();
          commit("setAccount", onboardState.address);
          await dispatch("setupContracts");

          // ensure we have reward per token so we can calculate the APY
          dispatch("retrieveRewardPerTokenFromStakingRewards");

          if (onSuccessCallback) {
            onSuccessCallback();
          }
        }
      }
    },
    async retrievePrices({ dispatch, commit }) {
      const mainnetStakingTokenAddress = DeployedStakingRewardsTokenContractAddresses[
        "1"
      ].toLowerCase();
      const res = await axios.get(
        `https://api.coingecko.com/api/v3/simple/token_price/ethereum?contract_addresses=${mainnetStakingTokenAddress}&vs_currencies=usd`
      );

      if (res.status === 200) {
        const usdRes = res.data[mainnetStakingTokenAddress];
        if (usdRes) {
          // DOCEDIT
          // console.log("StakingRewardsToken price in USD", usdRes.usd);
          commit("setStakingRewardTokenPriceInUsd", usdRes.usd);
        }
      }

      // uniswap
      const gqlUniswapClient = client({
        url: "https://api.thegraph.com/subgraphs/name/uniswap/uniswap-v2",
      });

      const subgraphUniswapRes = await gqlUniswapClient.query(`
      {
          pairs(where:{id: "${DeployedStakingTokenContractAddresses['1'][0]}"}) {
          id
          totalSupply
          token0 {
            id
          }
          token1 {
            id
          }
          token0Price
          token1Price
          reserve0
          reserve1
          reserveUSD
          reserveETH
        }
      }
      `);

      // balancer
      const gqlBalancerClient = client({
        url: "https://api.thegraph.com/subgraphs/name/balancer-labs/balancer",
      });

      const subgraphBalancerRes = await gqlBalancerClient.query(`{
        pools(first: 1, where: {id: "${DeployedStakingTokenContractAddresses['1'][1]}"}) {
          id
          totalShares
          liquidity
        }
      }  
      `);

      commit("setStakingTokenPriceInUsd", [
          subgraphUniswapRes.data.pairs[0].reserveUSD / subgraphUniswapRes.data.pairs[0].totalSupply,
          subgraphBalancerRes.data.pools[0].liquidity / subgraphBalancerRes.data.pools[0].totalShares,
        ]);

      commit("setStakingReserveUSD", [
        subgraphUniswapRes.data.pairs[0].reserveUSD,
        subgraphBalancerRes.data.pools[0].liquidity,
      ]);
    },
    async retrieveRewardPerTokenFromStakingRewards({ state, commit }) {

      // loads the current two live pools

      const res0 = await state.stakingRewardsContracts[0].rewardPerToken();
      const resRewardRate0 = await state.stakingRewardsContracts[0].rewardRate();

        // DOCEDIT
        // console.log('rewardPerToken 0', parseFloat(ethers.utils.formatEther(res0)));
        // console.log('rewardRate 0', parseFloat(ethers.utils.formatEther(resRewardRate0)));

      const res1 = await state.stakingRewardsContracts[1].rewardPerToken();
      const resRewardRate1 = await state.stakingRewardsContracts[1].rewardRate();

        // console.log('rewardPerToken 1', parseFloat(ethers.utils.formatEther(res1)));
        // console.log('rewardRate 1', parseFloat(ethers.utils.formatEther(resRewardRate1)));

      commit('setRewardPerToken', [
        parseFloat(ethers.utils.formatEther(res0)),
        parseFloat(ethers.utils.formatEther(res1))
      ]);
      commit('setRewardRate', [
        parseFloat(ethers.utils.formatEther(resRewardRate0)),
        parseFloat(ethers.utils.formatEther(resRewardRate1))
      ]);
    },
    async changeWallet({ dispatch }) {
      dispatch("bootstrap", { isChange: true });
    },
    async setupContracts({ commit, dispatch }) {
      const signer = provider.getSigner();
      const chain = await provider.getNetwork();

      commit("setChain", chain);

      let contracts = {};

      // DOCNOTE remove vesting stuff
      // wire up vesting contracts
      // const vestingContractAddresses = DeployedVestingContractAddresses[chain.chainId.toString()];
      // for (let i = 0; i < vestingContractAddresses.length; i++) {
      //   const address = vestingContractAddresses[i];
      //   contracts[`vesting_contract_${i}`] = new ethers.Contract(
      //     address,
      //     VestingContractABI,
      //     signer
      //   );
      // }

      // wire up vesting contracts without delegation
      // const vestingContractWithoutDelegationAddresses =
      //   DeployedVestingContractWithoutDelegationAddresses[
      //     chain.chainId.toString()
      //   ];
      // for (
      //   let i = 0;
      //   i < vestingContractWithoutDelegationAddresses.length;
      //   i++
      // ) {
      //   const address = vestingContractWithoutDelegationAddresses[i];
      //   contracts[
      //     `vesting_contract_without_delegation_${i}`
      //   ] = new ethers.Contract(
      //     address,
      //     VestingContractWithoutDelegationABI,
      //     signer
      //   );
      // }

      // commit("setContracts", contracts);
      // dispatch("getVestingSchedulesForUser");

      const stakingRewardsContracts = [];
      const stakingRewardsContractAddresses =
        DeployedStakingRewardsContractAddresses[chain.chainId.toString()];

      for (let i = 0; i < stakingRewardsContractAddresses.length; i++) {
        const address = stakingRewardsContractAddresses[i].address;
        stakingRewardsContracts.push(
          new ethers.Contract(address, IStakingRewardsABI, signer)
        );
      }

      commit("setStakingRewardsContracts", stakingRewardsContracts);

      const stakingRewardsTokenContract = new ethers.Contract(
        DeployedStakingRewardsTokenContractAddresses[chain.chainId.toString()],
        ERC20ABI,
        signer
      );

      commit("setStakingRewardsTokenContract", stakingRewardsTokenContract);

      const stakingTokenContracts = [];
      const stakingTokenContractAddresses =
        DeployedStakingTokenContractAddresses[chain.chainId.toString()];

      for (let i = 0; i < stakingTokenContractAddresses.length; i++) {
        const address = stakingTokenContractAddresses[i];
        stakingTokenContracts.push(
          new ethers.Contract(address, ERC20ABI, signer)
        );
      }

      commit("setStakingTokenContracts", stakingTokenContracts);

      dispatch("getStakingInfoForUser");
    },
    async getVestingSchedulesForUser({ state, commit }) {
      if (state.contracts && state.account) {
        const contracts = state.contracts;
        const account = state.account;

        const vestingSchedulesAcrossAllKnownContracts = await Promise.all(
          Object.keys(contracts).map(async (_contractKey) => {
            const vestingScheduleForBeneficiary = await contracts[
              _contractKey
            ].vestingScheduleForBeneficiary(account);

            // We need to check _amount first as if a schedule doesn't exist, the call to `availableDrawDownAmount`
            // will revert.
            const _availableDrawDownAmount =
              vestingScheduleForBeneficiary._amount.toString() === "0"
                ? 0
                : await contracts[_contractKey].availableDrawDownAmount(
                    account
                  );

            return {
              ...vestingScheduleForBeneficiary,
              _availableDrawDownAmount,
              _start: await contracts[_contractKey].start(),
              _end: await contracts[_contractKey].end(),
              _contractKey,
            };
          })
        );

        const activeVestingSchedulesAcrossAllKnownContracts = vestingSchedulesAcrossAllKnownContracts.filter(
          (vestingSchedule) => vestingSchedule._availableDrawDownAmount !== 0
        );

        const vestingSchedules = [];
        activeVestingSchedulesAcrossAllKnownContracts.forEach(
          (vestingSchedule) => {
            const {
              _amount,
              _totalDrawn,
              _remainingBalance,
              _availableDrawDownAmount,
              _start,
              _end,
              _contractKey,
            } = vestingSchedule;

            const vestingScheduleLengthInSeconds = parseInt(
              _end.sub(_start).toString()
            );
            vestingSchedules.push({
              totalLocked: ethers.utils.formatEther(_amount),
              remainingBalance: ethers.utils.formatEther(
                _remainingBalance.sub(_availableDrawDownAmount)
              ),
              totalDrawn: ethers.utils.formatEther(_totalDrawn),
              readyToClaim: ethers.utils.formatEther(_availableDrawDownAmount),
              start: parseInt(_start.toString()),
              end: parseInt(_end.toString()),
              vestingScheduleLengthInSeconds,
              vestingScheduleLengthHumanised: moment
                .duration(vestingScheduleLengthInSeconds, "seconds")
                .humanize(),
              contractKey: _contractKey,
            });
          }
        );

        commit("setVestingSchedules", vestingSchedules);
      }
    },
    async getStakingInfoForUser({ state, commit }) {
      if (
        state.stakingRewardsTokenContract &&
        state.stakingRewardsContracts &&
        state.stakingTokenContracts &&
        state.account
      ) {
        const account = state.account;

        const infoForAllStakingRewardsContracts = await Promise.all(
          state.stakingRewardsContracts.map(async (_, index) => {
            const stakingRewardsContract = await state.stakingRewardsContracts[
              index
            ];
            const stakingTokenContract = await state.stakingTokenContracts[
              index
            ];

            const rewardsTokenBalance = await state.stakingRewardsTokenContract.balanceOf(
              account
            );

            const rewardsTokenEarned = await stakingRewardsContract.earned(
              account
            );
            const stakedTokenBalance = await stakingRewardsContract.balanceOf(
              account
            );
            const stakingRewardsContractFinish = await stakingRewardsContract.periodFinish();

            const stakedTokenAllowance = await stakingTokenContract.allowance(
              account,
              stakingRewardsContract.address
            );
            const unstakedTokenBalance = await stakingTokenContract.balanceOf(
              account
            );

            return {
              name: DeployedStakingRewardsContractAddresses[state.chain.chainId.toString()][index].name,
              icon: DeployedStakingRewardsContractAddresses[state.chain.chainId.toString()][index].icon,
              message: DeployedStakingRewardsContractAddresses[state.chain.chainId.toString()][index].message,
              button: DeployedStakingRewardsContractAddresses[state.chain.chainId.toString()][index].button,
              stakedTokenUrl: DeployedStakingRewardsContractAddresses[state.chain.chainId.toString()][index].url,
              deprecated: DeployedStakingRewardsContractAddresses[state.chain.chainId.toString()][index].deprecated,

              rewardsTokenEarned: ethers.utils.formatEther(rewardsTokenEarned),
              rewardsTokenBalance: ethers.utils.formatEther(
                rewardsTokenBalance
              ),
              hasRewards: rewardsTokenEarned.gt("0"),
              stakingRewardsContractFinish: stakingRewardsContractFinish.toNumber(),

              stakedTokenBalance: ethers.utils.formatEther(stakedTokenBalance),
              stakedTokenAllowance: stakedTokenAllowance.gt("0"),
              hasStake: stakedTokenBalance.gt("0"),

              unstakedTokenBalance: ethers.utils.formatEther(
                unstakedTokenBalance
              ),
              hasUnstaked: unstakedTokenBalance.gt("0"),
            };
          })
        );

        // console.log("setStakingInfo", infoForAllStakingRewardsContracts);

        commit("setStakingInfo", infoForAllStakingRewardsContracts);
      }
    },
    async approveStakingTokens({ state, commit, dispatch }, index) {
      if (
        state.stakingTokenContracts &&
        state.stakingRewardsContracts &&
        state.account
      ) {
        const account = state.account;

        const tx = await state.stakingTokenContracts[index].approve(
          state.stakingRewardsContracts[index].address,
          MAX_UINT256,
          { from: account }
        );

        notify.hash(tx.hash);

        await tx.wait(1);

        dispatch("getStakingInfoForUser");
      }
    },
    async stakingGetRewards({ state, commit, dispatch }, index) {
      if (state.stakingRewardsContracts && state.account) {
        const account = state.account;

        const tx = await state.stakingRewardsContracts[
          index
         ].getReward({from: account });

        notify.hash(tx.hash);

        await tx.wait(1);

        dispatch("getStakingInfoForUser");
      }
    },
    async stake({ state, commit, dispatch }, { amtInEth, index }) {
      if (state.stakingRewardsContracts && state.account) {
        const account = state.account;

        const tx = await state.stakingRewardsContracts[
          index
        ].stake(ethers.utils.parseEther(amtInEth), { from: account });

        notify.hash(tx.hash);

        await tx.wait(1);

        dispatch("getStakingInfoForUser");
      }
    },
    async withdraw({ state, commit, dispatch }, { amtInEth, index }) {
      if (state.stakingRewardsContracts && state.account) {
        const account = state.account;

        const tx = await state.stakingRewardsContracts[
          index
        ].withdraw(ethers.utils.parseEther(amtInEth), { from: account });

        notify.hash(tx.hash);

        await tx.wait(1);

        dispatch("getStakingInfoForUser");
      }
    },
    async drawDown({ state }, contractKey) {
      const tx = await state.contracts[contractKey].drawDown();
      notify.hash(tx.hash);
    },
  },
  getters: {
    contracts: (state) => state.contracts,
    stakingRewardsContract: (state) => state.stakingRewardsContract,
    account: (state) => state.account,
    vestingSchedules: (state) => state.vestingSchedules,
    loadingVestingSchedules: (state) => state.loadingVestingSchedules,
    stakingInfo: (state) => state.stakingInfo,
    stakingRewardTokenPriceInUsd: (state) => state.stakingRewardTokenPriceInUsd,
    stakingTokenPriceInUsd: (state) => state.stakingTokenPriceInUsd,
    stakingReserveUSD: (state) => state.stakingReserveUSD,
    rewardPerToken: (state) => state.rewardPerToken,
    rewardRate: (state) => state.rewardRate,
  },
});
