<script>
    import {mapState, mapGetters} from 'vuex';
    import MainNav from '../components/MainNav';
    import StonkBrand from '@/components/StonkBrand';

    export default {
        components: {MainNav, StonkBrand},
        computed: {
          ...mapState([
            'onboard',
            'account',
          ]),
          ...mapGetters(['vestingSchedules', 'loadingVestingSchedules']),
        },
        methods: {
            connect() {
                if (this.account) {
                  this.$router.push({name: 'Stake'});
                  return;
                }
                this.$store.dispatch("bootstrap", {
                  onSuccessCallback: () => {
                    this.$router.push({name: 'Stake'});
                  },
                });
            },
            changeWallet() {
                this.$store.dispatch("changeWallet");
            },
        },
        data() {
          return {
            connectingModalOpen: false,
          }
        }
    };
</script>

